import React from "react";
import './Footer.css';
import FavoriteIcon from '@material-ui/icons/Favorite';

export default ({black})=>{
    return (
        <div className={black ? 'black' : ''}>
            <div className="footer--info">
                <a>
                    <h2 className="footer--credits"> Página Desenvolvida com {FavoriteIcon} por Beatriz Machado</h2><br/>
                    <h2 className="footer--credits"> Fonte de Dados - <strong>TMDB</strong></h2><br/>
                </a>
            </div>
        </div>
    );
}